body,
html {
  overflow-x: hidden;
  background: #f6efe8;
  position: relative;
}

a,
button {
  transition: all 0.3s;
}
a:hover,
button:hover {
  opacity: 0.5;
}

h2 {
  font: 700 42px/1.05952 "Salina-Trial", sans-serif;
  text-align: center;
  color: #1f2019;
}

h3 {
  font: 700 32px "Proxima Nova", sans-serif;
  color: #1f2019;
}

h4 {
  font: 700 24px/1.3 "Proxima Nova", sans-serif;
  color: #1f2019;
}

section {
  padding: 100px 0;
}

.btn__red {
  border-radius: 20px;
  padding: 20px 30px;
  background: #bf4720;
  font: 400 20px "Proxima Nova", sans-serif;
  text-align: center;
  color: #fff;
  width: max-content;
  display: block;
  border: none;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
  box-sizing: content-box;
}

.nav {
  padding: 24px 0 0;
}
.nav .container {
  max-width: 1720px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav__logo {
  font: 700 32px "Salina-Trial", sans-serif;
  color: #1f2019;
}
.nav__soc {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 18px;
}

.header {
  padding: 13px 0 100px;
  z-index: 2;
  position: relative;
}
.header .container {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 55px;
  align-items: center;
}
.header__prev {
  padding: 15% 14% 7% 15.7%;
  background: url("../img/header_bg.svg") center no-repeat;
  background-size: contain;
}
.header__prev img {
  width: 100%;
  border-radius: 150px 20px;
}
.header h1 {
  font: 700 32px/1.05952 "Proxima Nova", sans-serif;
  color: #1f2019;
}
.header__name {
  font: 900 79px/1.05952 "Salina-Trial", sans-serif;
  color: #1f2019;
  margin: 8px 0 0;
}
.header__info {
  max-width: 480px;
}
.header__text {
  margin: 30px 0 0;
  padding: 0 0 0 43px;
  position: relative;
  font: 400 24px "Proxima Nova", sans-serif;
  color: #bf4720;
}
.header__text::before {
  content: "";
  display: block;
  width: 100vw;
  height: 1px;
  background: #bf4720;
  position: absolute;
  top: 14px;
  right: calc(100% - 24px);
}
.header .btn__red {
  margin: 50px 0 0;
}

.trouble {
  position: relative;
  z-index: 1;
}
.trouble__tag {
  margin: 80px 0 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  gap: 15px;
}
.trouble__tag p {
  font: 400 20px "Proxima Nova", sans-serif;
  color: #bf4720;
  border: 1px solid #bf4720;
  border-radius: 50px;
  padding: 19px 33px;
  transition: all 0.3s;
  cursor: pointer;
}
.trouble__tag p.active {
  background: #bf4720;
  color: #fff;
}
.trouble__wrapper {
  margin: 50px 0 0;
  position: relative;
}
.trouble__slide {
  display: grid;
  grid-template-columns: 480px 1fr;
  gap: 0;
  background: #f1dfd4;
  position: relative;
  border-radius: 20px;
  overflow: hidden;
  min-height: 466px;
}
.trouble__prev {
  position: relative;
}
.trouble__prev img {
  width: 100%;
  height: 1px;
  min-height: 100%;
  object-fit: cover;
}
.trouble__prev::before {
  content: "";
  display: block;
  bottom: 0;
  left: 0;
  top: 0;
  width: 100%;
  position: absolute;
  background: url("../img/trouble_bg.png") left bottom no-repeat;
  background-size: contain;
}
.trouble__info {
  padding: 50px;
}
.trouble__info p {
  margin: 24px 0 0;
  font: 400 18px/1.35 "Proxima Nova", sans-serif;
  color: #1f2019;
}
.trouble .swiper-button-next,
.trouble .swiper-button-prev {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  border-radius: 100%;
  background: #bf4720;
  margin: 0;
  transform: translateY(-50%);
  top: 50%;
  right: calc(100% + 50px);
  left: auto;
  transition: all 0.3s;
}
.trouble .swiper-button-next:hover,
.trouble .swiper-button-prev:hover {
  opacity: 0.5;
}
.trouble .swiper-button-next::after,
.trouble .swiper-button-prev::after {
  content: none;
}
.trouble .swiper-button-next svg,
.trouble .swiper-button-prev svg {
  width: 24px;
  height: 24px;
}
.trouble .swiper-button-next {
  right: auto;
  left: calc(100% + 50px);
}
.trouble::before {
  content: "";
  display: block;
  bottom: 506px;
  left: 0;
  width: 313px;
  height: 757px;
  background: url("../img/trouble_flover.png") left no-repeat;
  background-size: contain;
  position: absolute;
  z-index: -1;
}

.consul {
  position: relative;
  z-index: 1;
}
.consul__wrapper {
  margin: 80px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 16px;
}
.consul__item {
  border-radius: 20px;
  padding: 230px 50px 50px 50px;
  background: #f1dfd4;
  position: relative;
  overflow: hidden;
  z-index: 1;
}
.consul__item h3 {
  margin: 24px 0 0;
}
.consul__prev {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: -1;
}
.consul__prev img {
  width: 100%;
  height: 275px;
  object-fit: cover;
}
.consul__prev::before {
  content: "";
  display: block;
  width: 100%;
  height: 230px;
  position: absolute;
  bottom: 0;
  left: 0;
  background: linear-gradient(180deg, rgba(241, 223, 212, 0) 0%, #f1dfd4 100%);
}
.consul__num {
  font: 700 42px/1.05952 "Salina-Trial", sans-serif;
  color: #bf4720;
}
.consul__text {
  margin: 24px 0 0;
  font: 400 18px/1.33333 "Proxima Nova", sans-serif;
  color: #1f2019;
}
.consul .btn__red {
  margin: 50px auto 0;
}
.consul::before {
  content: "";
  display: block;
  bottom: 530px;
  right: 0;
  width: 313px;
  height: 757px;
  background: url("../img/trouble_flover.png") left no-repeat;
  background-size: contain;
  position: absolute;
  transform: scaleX(-1);
  z-index: -1;
}

.faq {
  position: relative;
  z-index: 1;
}
.faq::before {
  content: "";
  display: block;
  top: 0;
  transform: translateY(-50%);
  left: 0;
  width: 313px;
  height: 757px;
  background: url("../img/trouble_flover.png") left no-repeat;
  background-size: contain;
  position: absolute;
  z-index: -1;
}
.faq__wrapper {
  margin: 80px 0 0;
  border-bottom: 1px solid #bf4720;
}
.faq__text {
  max-height: 0;
  overflow: hidden;
  padding: 0;
}
.faq__text p {
  font: 400 18px/1.35 "Proxima Nova", sans-serif;
  color: #1f2019;
  padding: 0 0 32px;
}
.faq__btn {
  border-top: 1px solid #bf4720;
  position: relative;
  padding: 32px 32px 24px 0;
}
.faq__btn span {
  position: absolute;
  top: 39px;
  right: 0;
  transition: all 0.3s;
}
.faq__btn .close {
  opacity: 0;
}
.faq__btn .open {
  opacity: 1;
}
.faq__btn p {
  font: 400 24px/1.6 "Proxima Nova", sans-serif;
  color: #1f2019;
}
.faq__btn.active .close {
  opacity: 1;
}
.faq__btn.active .open {
  opacity: 0;
}
.faq__btn.active + .faq__text {
  max-height: none;
}

.price {
  position: relative;
  z-index: 1;
}
.price::before {
  content: "";
  display: block;
  top: 0;
  right: 0;
  width: 313px;
  height: 757px;
  background: url("../img/trouble_flover.png") left no-repeat;
  background-size: contain;
  position: absolute;
  transform: scaleX(-1) translateY(-50%);
  z-index: -1;
}
.price__wrapper {
  margin: 80px 0 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 16px;
}
.price__item {
  border-radius: 20px;
  padding: 50px;
  background: #f1dfd4;
  display: grid;
  grid-template-columns: 2fr 3fr 3fr auto;
  gap: 20px;
  align-items: start;
}
.price__item ul {
  display: grid;
  grid-template-columns: 1fr;
  gap: 16px;
}
.price__item ul p {
  font: 400 18px/1.3 "Proxima Nova", sans-serif;
  color: #1f2019;
}
.price__item ul li {
  display: grid;
  grid-template-columns: 20px 1fr;
  gap: 12px;
}
.price__item ul li::before {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  background: url("../img/check.svg") center no-repeat;
  background-size: contain;
}
.price__price p {
  font: 700 18px/1.3 "Proxima Nova", sans-serif;
  color: #bf4720;
}
.price__price p + p {
  font: 700 50px/1.3 "Salina-Trial", sans-serif;
  color: #1f2019;
}

.comment {
  position: relative;
}
.comment .swiper-button-next,
.comment .swiper-button-prev {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  border-radius: 100%;
  background: #bf4720;
  margin: 0;
  transform: translateY(-50%);
  top: 50%;
  right: calc(100% + 50px);
  left: auto;
  transition: all 0.3s;
}
.comment .swiper-button-next:hover,
.comment .swiper-button-prev:hover {
  opacity: 0.5;
}
.comment .swiper-button-next::after,
.comment .swiper-button-prev::after {
  content: none;
}
.comment .swiper-button-next svg,
.comment .swiper-button-prev svg {
  width: 24px;
  height: 24px;
}
.comment .swiper-button-next {
  right: auto;
  left: calc(100% + 50px);
}
.comment__wrapper {
  position: relative;
  margin: 80px 0 0;
}
.comment__slide {
  border-radius: 20px;
  padding: 50px;
  background: #f1dfd4;
}
.comment__name {
  display: grid;
  grid-template-columns: 90px 1fr;
  gap: 24px;
  align-items: center;
}
.comment__name img {
  width: 90px;
  height: 90px;
  border-radius: 100%;
}
.comment__name p {
  font: 700 32px/1.3 "Proxima Nova", sans-serif;
  color: #1f2019;
}
.comment__name p + p {
  margin: 6px 0 0;
  font: 400 18px/1.3 "Proxima Nova", sans-serif;
  color: #bf4720;
}
.comment__text {
  margin: 30px 0 0;
}
.comment__text p {
  font: 400 18px/1.35 "Proxima Nova", sans-serif;
  color: #1f2019;
}

.contact {
  position: relative;
  z-index: 1;
}
.contact::before {
  content: "";
  display: block;
  top: 0;
  transform: translateY(-50%);
  left: 0;
  width: 313px;
  height: 757px;
  background: url("../img/trouble_flover.png") left no-repeat;
  background-size: contain;
  position: absolute;
  z-index: -1;
}
.contact__wrapper {
  margin: 80px 0 0;
  display: grid;
  grid-template-columns: 1fr 478px;
  gap: 16px;
}
.contact__map {
  border-radius: 20px;
  position: relative;
  overflow: hidden;
}
.contact__map iframe {
  height: 100%;
  min-height: 300px;
}
.contact__info {
  padding: 50px;
  border-radius: 20px;
  background: #f1dfd4;
  display: grid;
  grid-template-columns: 1fr;
  gap: 24px;
}
.contact__info li {
  display: grid;
  grid-template-columns: 32px 1fr;
  gap: 24px;
  align-items: start;
}
.contact__info a,
.contact__info p {
  font: 400 24px "Proxima Nova", sans-serif;
  color: #1f2019;
}
.contact__info a:hover {
  opacity: 1;
  color: #bf4720;
}

.footer {
  border-radius: 20px 20px 0 0;
  background: #f1dfd4;
  padding: 23px 0;
  margin: 100px 0 0;
}
.footer p {
  font: 400 16px/1.2 "Inter", sans-serif;
  text-align: center;
  color: #bf4720;
}

@media (max-width: 1440px) {
  section {
    padding: 80px 0;
  }
  h2 {
    font-size: 36px;
  }
  h3 {
    font-size: 24px;
  }
  h4 {
    font-size: 20px;
  }
  .nav .container {
    max-width: 1170px;
  }
  .nav__logo {
    font-size: 24px;
  }
  .header {
    padding: 20px 0 80px;
  }
  .header .container {
    grid-template-columns: 1fr 507px;
  }
  .header h1 {
    font-size: 24px;
  }
  .header__name {
    font-size: 64px;
  }
  .header__text {
    font-size: 20px;
  }
  .header .btn__red {
    margin: 50px 0 0;
  }
  .trouble::before,
  .consul::before,
  .faq::before,
  .price::before,
  .contact::before {
    content: none;
  }
  .trouble__tag {
    margin: 50px 0 0;
  }
  .trouble__tag p {
    font-size: 18px;
  }
  .trouble__slide {
    min-height: 400px;
  }
  .trouble__info p {
    margin: 16px 0 0;
    font-size: 16px;
  }
  .consul__wrapper {
    margin: 50px 0 0;
  }
  .consul__num {
    font-size: 36px;
  }
  .consul__text {
    font-size: 16px;
    margin: 16px 0 0;
  }
  .consul h3 {
    max-width: 220px;
    margin: 20px 0 0;
  }
  .consul .btn__red {
    margin: 40px auto 0;
  }
  .faq__wrapper {
    margin: 50px 0 0;
  }
  .faq__btn p {
    font-size: 20px;
  }
  .faq__text p {
    font-size: 16px;
  }
  .price__item ul p {
    font-size: 16px;
  }
  .price__price p {
    font-size: 16px;
  }
  .price__price p + p {
    font-size: 48px;
  }
  .comment__wrapper,
  .contact__wrapper {
    margin: 50px 0 0;
  }
  .contact__info a,
  .contact__info p {
    font-size: 20px;
  }
  .contact__info li {
    gap: 16px;
  }
  .trouble__wrapper,
  .comment__wrapper {
    padding: 0 16px;
  }
  .trouble .swiper-button-next,
  .trouble .swiper-button-prev,
  .comment .swiper-button-next,
  .comment .swiper-button-prev {
    right: -12px;
    left: auto;
    width: 24px;
    height: 24px;
  }
  .trouble .swiper-button-next svg,
  .trouble .swiper-button-prev svg,
  .comment .swiper-button-next svg,
  .comment .swiper-button-prev svg {
    width: 16px;
    height: 16px;
  }
  .trouble .swiper-button-prev,
  .comment .swiper-button-prev {
    left: -12px;
    right: auto;
  }
}
@media (max-width: 1024px) {
  .header {
    padding: 36px 0 80px;
  }
  .header .container {
    grid-template-columns: 1fr;
  }
  .header__prev {
    max-width: 600px;
    margin: 0 auto;
  }
  .header__prev img {
    border-radius: 100px 15px;
  }
  .trouble__slide {
    grid-template-columns: 1fr;
  }
  .trouble__prev::before {
    background: url("../img/trouble_mob.png") left top no-repeat;
    background-size: cover;
  }
  .trouble__prev img {
    height: 575px;
  }
  .trouble__info {
    padding: 24px;
  }
  .consul__item {
    padding: 196px 24px 24px;
  }
  .price__item {
    grid-template-columns: 1fr;
    padding: 24px;
  }
  .comment__slide {
    padding: 24px;
  }
  .contact__info {
    padding: 24px;
  }
  .contact__wrapper {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 768px) {
  section {
    padding: 50px 0;
  }
  h2 {
    font-size: 32px;
  }
  .nav__logo {
    font-size: 20px;
  }
  .header {
    padding: 36px 0 50px;
  }
  .header__name {
    font-size: 48px;
  }
  .header .container {
    gap: 40px;
  }
  .trouble__tag {
    gap: 8px;
  }
  .trouble__tag p {
    font-size: 14px;
    padding: 8px 15px;
  }
  .trouble__prev img {
    height: 274px;
  }
  .consul__wrapper {
    grid-template-columns: 1fr;
  }
  .consul__prev img {
    height: 235px;
  }
  .price__price p + p {
    font-size: 36px;
  }
  .comment__name {
    grid-template-columns: 63px 1fr;
    gap: 16px;
  }
  .comment__name img {
    width: 63px;
    height: 63px;
  }
  .comment__name p {
    font-size: 24px;
  }
  .comment__name p + p {
    font-size: 16px;
  }
  .comment__text {
    margin: 24px 0 0;
  }
  .comment__text p {
    font-size: 16px;
  }
  .contact__wrapper {
    grid-template-columns: 1fr;
    gap: 16px;
  }
  .contact__map iframe {
    height: 215px;
    min-height: 215px;
  }
  .contact__info a,
  .contact__info p {
    font-size: 16px;
  }
  .contact__info li {
    gap: 14px;
    grid-template-columns: 24px 1fr;
  }
  .contact__info li svg {
    width: 24px;
    height: 24px;
  }
  .footer {
    margin: 0;
  }
}